:root {
  /* primary colors */
  --cyan: hsl(180, 66%, 49%);
  --darkViolet: hsl(257, 27%, 26%);
  /* secondary colors */
  --red: hsl(0, 87%, 67%);
  /* neutral colors */
  --gray: hsl(0, 0%, 89%);
  --grayishViolet: hsl(257, 7%, 63%);
  --veryDarkBlue: hsl(255, 11%, 22%);
  --veryDarkViolet: hsl(260, 8%, 14%);
}

html {
  scroll-behavior: smooth;
}

/* reset and font */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

/* mobile first ;) */
/* header styles */
header {
  display: flex;
  width: 85%;
  justify-content: space-between;
  align-items: center;
  margin: 2rem auto;
  position: relative;
}
header img {
  height: 2em;
  width: 45%;
}
header ion-icon {
  font-size: 2.5rem;
  opacity: 0.5;
}
.nav-links {
  position: absolute;
  background: var(--veryDarkBlue);
  top: 16em;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 23rem;
  border-radius: .8em;
  text-align: center;
}
.nav-links ul {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.nav-links ul a {
  text-decoration: none;
  color:white;
}
.nav-links-e {
  margin: 2rem auto;
  width: 80%;
  
}
.nav-links-e *{
  margin: 1.3rem;
}
.nav-links .btns {
  display: flex;
  flex-direction: column;
  margin-top: -1rem;
}
hr {
  width: 75%;
  opacity: 0.1;
}
#login {
  background: var(--veryDarkBlue);
  border: none;
  width: 50%;
  margin: 1rem auto .5rem auto;
  padding: 1em;
  font-size: 1em;
  font-family: inherit;
}
#signup {
  background: var(--cyan);
  color: white;
  border: none;
  width: 80%;
  margin: auto;
  padding: .75em;
  border-radius:2rem;
  font-size: 1em;
  font-family: inherit;
}

/* hero styles */
.hero-illustration {
  width: 100vw;
  margin: auto;
  overflow-x: hidden;
  text-align: center;
}
.hero-illustration img {
  height: 20rem;
  margin-left: 1rem;
}
.hero-illustration h1 {
  font-size: 2.2rem;
  line-height: 2.5rem;
  color: var(--veryDarkViolet);
  width: 90%;
  margin: 2rem auto 14px auto;
}
.hero-illustration p {
  color: var(--grayishViolet);
  width: 85%;
  margin: 3.5rem auto 0 auto;
}

.get-started-btn {
  background: var(--cyan);
  color: white;
  border: none;
  width: 50%;
  margin: -1.5rem auto .5rem auto;
  padding: .75em;
  border-radius:2rem;
  font-size: 1em;
  font-family: inherit;
  cursor: pointer;
}

/* shorten it styles */
.shorten {
  position: relative;
}
.shorten-it {
  background-image: url(./images/bg-shorten-mobile.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--veryDarkViolet);
  background-position-x: 5rem;
  background-position-y: -2em;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 80%;
  margin: 3rem auto 1.5rem auto;
  border-radius: .7rem;
  position: relative;
  z-index: 1;
}
.shorten-it * {
  width: 80%;
  margin: .5rem auto;
  padding: .75rem;
  border-radius: .5rem;
  font-family: inherit;
  border: none;
}

.shorten-it input {
  outline: none;
  caret-color: var(--veryDarkBlue);
  color: var(--veryDarkBlue);
}
.empty {
  color: var(--red);
  border: 2px solid var(--red);
}
.shorten-it small {
  color: var(--red);
  margin-top: -1rem;
  margin-left: 0;
  font-style: italic;
}
.shorten-it button { 
  background: var(--cyan);
  color: white;
  width: 90%;
  font-weight: 700;
  cursor: pointer;
}
.copied {
  background: var(--veryDarkViolet) !important;
}

.links-container {
  background: var(--gray);
  margin: 0;
  min-height: 14rem;
  position: relative;
  padding-top: 10rem;
  bottom: 7rem;
}

.links {
  width: 80%;
  max-height: fit-content;
  margin: 0 auto 0 auto;
  border-radius: .5rem;
  padding: 1rem;
  position: relative;
  background: #fff;
}
.links h3:nth-child(1) {
  color: var(--veryDarkBlue);
  max-height: 1rem;
  overflow: hidden;
  background-color: #fff;
  border-bottom: 1px solid var(--gray);
  padding-bottom: 1rem;
  margin-top: 0;
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
}
.links h3:nth-child(2) {
  color: var(--cyan);
  font-weight: 500;
  font-size: 1rem;
}
.links button {
  width: 100%;
  color: #fff;
  font-family: inherit;
  font-size: 1rem;
  background: var(--cyan);
  margin: 0 auto .5rem auto;
  border: none;
  border-radius: .5rem;
  padding: .7rem;
}

.advanced-stats {
  background: var(--gray);
  padding: 7rem 0;
  margin: -8rem auto;
  width: 80%;
  position: relative;
}
.advanced-stats h2 {
  color: var(--veryDarkViolet);
}
.advanced-stats p {
  margin-top: 3rem;
  color: var(--grayishViolet);
}

/* perk styles */
.perks-container {
  text-align: center;
  background: var(--gray);
  margin-top: -5rem;
  padding-bottom: 3rem;
  position: relative;
  top: 1rem;
}
.perks-card {
  background: white;
  width: 75%;
  margin: 2rem auto 6rem auto;
  border-radius: .5rem;
  padding: 1rem;
  position: relative;
  max-height: 16rem;
}
.perks-card .icon {
  background: var(--veryDarkBlue);
  width: 5rem;
  height: 5rem;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: relative;
  bottom: 3.5rem;
}
.perks-card h3, p {
  position: relative;
  bottom: 2.5rem;
}
.perks-card h3 {
  color: var(--veryDarkViolet);
}
.perks-card p {
  color: var(--grayishViolet);
}
.perks-card .stick {
  width: 8px;
  background: cyan;
  height: 7rem;
  position: absolute;
  left: 50%;
  right: 50%;
  transform: translateX(-50%);
  top: -6rem;
  z-index: 0;
}

/* boost styles */
.boost {
  background: var(--darkViolet);
  background-image: url(./images/bg-boost-mobile.svg);
  background-repeat: no-repeat;
  background-position: center;
  color: white;
  text-align: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.boost h2 {
  margin-bottom: 1.7rem;
}

/* footer styles */
footer {
  background: var(--veryDarkBlue);
  color: white;
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
footer ul {
  padding-left: 0;
}
footer ul li {
  list-style: none;
}
footer a {
  color:var(--gray);
  font-family: inherit;
  text-decoration: none;
}
footer ion-icon {
  height: 1.8rem;
  width: 1.8rem;
  margin: .5rem;
  cursor: pointer;
}
.desktop-header {
  display: none;
}

/* desktop styles */
@media screen and (min-width: 640px) {
  * {
    position: static;
  }
  .menu-icon, .nav-links, .mobile-logo {
    display: none;
  }
  header img {
    width: 7vw;
  }
  header {
    max-width: 75vw;
  }

  .desktop-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    width: 100%;
  }
  .desktop-header .btns {
    margin: 0 auto;
  }
  #login {
    margin: 0 auto;
  }
  #signup {
    padding-top: .5rem;
    padding-bottom: .5rem;
    margin: .5rem auto .5rem auto;
    width: 7rem;
  }
  .nav-links-desktop {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .nav-links-desktop ul {
    display: flex;
    text-decoration: none;
  }
  .nav-links-desktop ul li {
    list-style: none;
    text-decoration: none;
  }
  .nav-links-desktop ul li a {
    text-decoration: none;
    color: var(--grayishViolet);
    margin-right: 2rem;
  }
  .btns {
    display: flex;
  }
  .btns li {
    list-style: none;
    text-decoration: none;
  }
  .btns li a {
    color: #fff;
    text-decoration: none;
  }
  #login {
    background-color: #fff;
    margin-right: 1rem;
  }
  #login a {
    color: var(--grayishViolet);
  }

  .hero-illustration {
    display: flex;
    flex-direction: row-reverse;
    margin-bottom: 4rem;
    text-align: left;
    width: 100%;
  }
  .hero-illustration div:nth-child(1) {
    width: 50%;
  }
  .hero-illustration div:nth-child(2) {
    width: 45%;
    margin-left: 10rem;
  }
  .hero-illustration div:nth-child(1) img {
    height: 25rem;
    position: relative;
    right: -5rem;
    overflow-x: visible;
  }
  .hero-illustration div:nth-child(2) h1{
    font-size: 4rem;
    line-height: 4.5rem;
    margin-left: 0;
  }
  .hero-illustration div:nth-child(2) p{
   margin: .8rem 0 3rem 0;
   position: static;
  }

  .hero-illustration div:nth-child(1) {
    width: 50%;
  }
  
  .shorten-it {
    width: 75%;
    height: 7rem;
    display: grid;
    grid-template-columns: 50%, 20%, 20%;
    grid-template-rows: 1rem 4rem 1rem;
    grid-gap: .8rem;
    align-items: center;
    background-image: url(./images/bg-shorten-desktop.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--darkViolet);
  }
  .shorten-it input {
    height: 2rem;
    width: 110%;
    grid-column: 1/3;
    grid-row: 2;
    margin-left: 2rem;
    margin-top: 0;
  }
  .shorten-it button {
    height: 3.5rem;
    width: 50%;
    margin-top: 0;
    margin-right: 2rem;
    grid-column: 3;
    grid-row: 2;
    justify-self: end;
  }
  .shorten-it small {
    margin-left: 1.3rem;
    margin-top: -1rem;
    grid-column: 1;
    grid-row: 3;
  }

  /* generated links styles */
  .links {
    width: 75%;
    height: 3rem;
    margin-bottom: 0;
    display: flex;
    align-items: center;
    flex-direction: row;
  }
  .links h3:nth-child(1) {
    max-width: 50%;
    margin-right: 5rem;
    margin-top: 1.5rem;
    border: none;
  }
  .links button {
    cursor: pointer;
    width: 10%;
    margin-right: 0;
  }
  
  .links-container {
    background: var(--gray);
    margin: 0;
    min-height: 14rem;
    position: relative;
    padding-top: 10rem;
  }
  .perks-container {
    margin-top: -9rem;
  }

  /* advanced stats styles*/
  .advanced-stats {
    width: 40%;
    margin: 0 auto -12rem auto;
  }
  .advanced-stats h2 {
    font-size: 2rem;
  }
  .advanced-stats p {
    color: var(--grayishViolet);
  }


  .stick {
    display: none;
  }
  .desktop-stick {
    height: 10px;
    width: 50vw;
    background-color: var(--cyan);
    position: absolute;
    top: 60%;
    left: 15%;
    transform: translateY(-60%);
    z-index: 0;
  }
  .perks-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 2rem 2rem 10rem 2rem 2rem;
    margin-bottom: 8rem;
    position: relative;
    width: 79%;
    margin: 7rem auto;
  }
  .perks-grid .perks-card {
    min-height: 10rem;
    width: 80%;
    text-align: left;
    padding: 1.5rem;
  }
  .perks-grid .perks-card h3 {
    margin-top: 0;
  }
  .perks-grid .perks-card p {
    font-size: .8rem;
  }

  .perks-grid .perks-card:nth-child(1) {
    grid-column: 1;
    grid-row: 1 / 4;
    position: static;
  }
  .perks-grid .perks-card:nth-child(2) {
    grid-column: 2;
    grid-row: 2 / 5
  }
  .perks-grid .perks-card:nth-child(3) {
    grid-column: 3;
    grid-row: 3 / 6;
  }
  .perks-grid .icon {
    right: 5rem;
    bottom: 4rem;
  }

  .boost {
    background-image: url(./images/bg-boost-desktop.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--darkViolet);
  }
  .boost h2 {
    font-size: 2rem;
  }
  .boost button {
    width: 12%;
  }
  
  footer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    padding: 4rem;
    text-align: left;
  }
  footer * {
    margin-top: 0 !important;
  }

  /* active states styles */
  header .nav-links-desktop ul a:hover, #login a:hover {
    color: var(--darkViolet);
  }
  #signup:hover {
    opacity: .5;
  }
  .get-started-btn:hover, .shorten-it button:hover {
    opacity: .8;
  }
  footer ul li a:hover {
    color: var(--cyan);
  }
  footer ion-icon:hover {
    color: var(--cyan);
  }
}


@media (max-width: 640px) and (min-width: 540px)  {
  .shorten-it button {
    width: 85%;
  }
}

@media (min-width: 320px) and (max-width: 411px) {
  .shorten-it button {
    width: 88%;
  }
}


/* ATTRIBUTION */
.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}